import axios from "axios";
import router from "@/router";

const state = {
  //   
  accessToken: null,
  refreshToken: null,
  accessTokenExpires: null,
  refreshTokenExpires: null,

  isLoading: false
};

const mutations = {
  //  Mutation
  login(state, payload) {
    state.accessToken = payload.accessToken;
    state.refreshToken = payload.refreshToken;
    state.accessTokenExpires = payload.accessTokenExpires;
    state.refreshTokenExpires = payload.refreshTokenExpires;
  },

  //  Mutation
  logout() {
    // Vuex  .
    location.reload();
  },

  fetchNewToken(state, payload) {
    state.accessToken = payload.accessToken;
    state.accessTokenExpires = payload.accessTokenExpires;
  },

  // Loading Flag Mutation
  fetchLoading(state, payload) {
    state.isLoading = payload;
  }
};

const actions = {
  login({ commit }, payload) {
    // Loading Flag true .
    commit("fetchLoading", true);

    axios
      .post(`user/login`, payload)
      .then(response => {
        const data = response.data;

        //  Date  
        const accessTokenExpires = new Date(data.access_expiration_date);
        const refreshTokenExpires = new Date(data.refresh_expiration_date);

        // State  
        const tokenData = {
          accessToken: data.access,
          refreshToken: data.refresh,
          accessTokenExpires: accessTokenExpires.toString(),
          refreshTokenExpires: refreshTokenExpires.toString()
        };

        //   Vuex State .
        commit("login", tokenData);

        localStorage.setItem("accessToken", tokenData.accessToken);
        localStorage.setItem("refreshToken", tokenData.refreshToken);
        localStorage.setItem(
          "accessTokenExpires",
          tokenData.accessTokenExpires
        );
        localStorage.setItem(
          "refreshTokenExpires",
          tokenData.refreshTokenExpires
        );

        //   ,   .
        router.replace("/main");
      })
      .catch(error => {
        //   ,      .
        const errorResponse = error.response;

        // Bad Request (400)  
        if (errorResponse.status == 400) {
          if (errorResponse.data["email"] && errorResponse.data["password"]) {
            alert(",  .");
          } else if (errorResponse.data["email"]) {
            alert(" .");
          } else if (errorResponse.data["password"]) {
            alert(" .");
          } else {
            alert("HTTP 400 -  .");
          }
        }
        // Unauthorized (401)  
        else if (errorResponse.status == 401) {
          alert("    .  .");
        }
        // Not Found(404)  
        else if (errorResponse.status == 404) {
          alert("HTTP 404 -   .   .");
        }
      })
      .finally(() => {
        // Loading Flag false .
        commit("fetchLoading", false);
      });
  },

  logout({ commit }) {
    // Vuex State    .
    commit("logout");

    localStorage.removeItem("accessToken");
    localStorage.removeItem("refreshToken");
    localStorage.removeItem("accessTokenExpires");
    localStorage.removeItem("refreshTokenExpires");
  },

  register({ commit }, registerFormData) {
    // Loading Flag true .
    commit("fetchLoading", true);

    //     HTTP POST .
    axios
      .post(`user/register`, registerFormData)
      .then(() => {
        alert(" .  .");

        //   ,   .
        router.replace("/auth");
      })
      .catch(error => {
        //   ,     .
        const errorResponse = error.response;

        // Bad Request (400)  
        if (errorResponse.status == 400) {
          // Error Response Email   ,
          if (errorResponse.data["email"]) {
            alert("     .");
          }
          //     ,
          else {
            alert("HTTP 400 -  .");
          }
        }
        // Unauthorized (401)  
        else if (errorResponse.status == 401) {
          alert("HTTP 401 -     .");
        }
        // Not Found(404)  
        else if (errorResponse.status == 404) {
          alert("HTTP 404 -   .   .");
        }
      })
      .finally(() => {
        // Loading Flag false .
        commit("fetchLoading", false);
      });
  },

  // localStorage    ,     .
  async autoLogin({ commit, dispatch }) {
    // localStorage   .
    const accessToken = localStorage.getItem("accessToken");
    const refreshToken = localStorage.getItem("refreshToken");
    const accessExpires = localStorage.getItem("accessTokenExpires");
    const refreshExpires = localStorage.getItem("refreshTokenExpires");

    //    ,  .
    if (!accessToken && !refreshToken) {
      return;
    }

    //      .
    const accessTimeLeft = new Date(accessExpires) - new Date();
    const refreshTimeLeft = new Date(refreshExpires) - new Date();

    //     ,  (refresh)  .
    // true:   , false:   , null:   .
    const flag = isRefreshable(accessTimeLeft, refreshTimeLeft);

    //    ,
    if (flag == false) {
      //  .
      await dispatch("logout");

      alert("   .  .");

      return;
    }

    const tokenData = {
      accessToken,
      refreshToken,
      accessExpires,
      refreshExpires
    };

    //    ,
    if (flag == null) {
      commit("login", tokenData);

      return;
    }

    // [] await   HTTP       .
    await axios
      //   HTTP POST    AccessToken 
      .post("user/refresh", {
        refresh: refreshToken
      })
      .then(({ data }) => {
        const newAccessToken = data.access;
        const newAccessTokenExpires = new Date(data.access_expiration_date);

        localStorage.setItem("accessToken", newAccessToken);
        localStorage.setItem("accessTokenExpires", newAccessTokenExpires);

        //    State .
        commit("fetchNewToken", {
          accessToken: newAccessToken,
          accessTokenExpires: newAccessTokenExpires
        });
      })
      .catch(error => {
        console.log("Failed", error.response.data);

        alert("  .  .");

        dispatch("logout");
      });
  }
};

const getters = {
  //   
  hasAccessToken(state) {
    return state.accessToken !== null;
  },
  //   
  isLoading(state) {
    return state.isLoading;
  }
};

const isRefreshable = (accessTimeLeft, refreshTimeLeft) => {
  if (accessTimeLeft < 0 && refreshTimeLeft > 0) {
    return true;
  } else if (refreshTimeLeft < 0) {
    return false;
  } else {
    return null;
  }
};

export default {
  // Vuex   
  namespaced: true,
  state,
  mutations,
  actions,
  getters
};
